import React from "react";
import CircularProgress from "@material-ui/core/CircularProgress";
import withStyles from "@material-ui/core/styles/withStyles";
import clsx from "clsx";
import PropTypes from "prop-types";

const styles = theme => ({ root: {} });

const CircularLoader = ({ classes, className, color = "secondary" }) => {
    return <CircularProgress className={clsx(classes.progress, className)} color={color} />;
};

CircularLoader.propTypes = { classes: PropTypes.object.isRequired };

export default withStyles(styles, { withTheme: true })(CircularLoader);
